<!-- eslint-disable no-unused-vars -->
<template>
	<div class="viewModule">
		<div class="title">
			<h1>{{ msg }} {{ status }}</h1>
		</div>
		<div class="vc">

		</div>
	</div>
</template>

<script setup>
import { defineProps, onMounted, watch } from 'vue';
import * as ECharts from "@/assets/echarts.min.js"

import LineProcess from '@/js/LineProcess'
import OptionList from '@/js/OptionList'
import PieProcess from '@/js/PieProcess'
import BarProcess from '@/js/BarProcess'
import ColumnBarProcess from '@/js/ColumnBarProcess'
import BarLineProcess from '@/js/BarLineProcess'
import PiedProcess from '@/js/PiedProcess'
import ScattersProcess from '@/js/ScattersProcess'
import ScatterProcess from '@/js/ScatterProcess'

const props = defineProps(['msg', 'status'])

let chartIDList = Array('#line', '#bar', '#columnBar', '#bar_line', '#pie', '#pied', '#scatters', '#scatter')

// option
function setOpt(id, optList) {
	switch (id) {
		case "#line":
			new LineProcess(optList).ready()
			return optList.retOpt("#line")
		case "#bar":
			new BarProcess(optList).ready()
			return optList.retOpt("#bar")
		case "#columnBar":
			new ColumnBarProcess(optList).ready()
			return optList.retOpt("#columnBar")
		case "#bar_line":
			new BarLineProcess(optList).ready()
			return optList.retOpt("#bar_line")
		case "#pie":
			new PieProcess(optList).ready()
			return optList.retOpt("#pie")
		case "#pied":
			new PiedProcess(optList).ready()
			return optList.retOpt("#pied")
		case "#scatters":
			new ScattersProcess(optList).ready()
			return optList.retOpt("#scatters")
		case "#scatter":
			new ScatterProcess(optList).ready()
			return optList.retOpt("#scatter")
	}
}

onMounted(() => {
	let optList = new OptionList()
	let chart = ECharts.init(document.querySelector(".vc"))

	//  chartresize
	window.addEventListener("resize", () => {
		chart.resize()
	})

	// Module2
	chart.setOption(setOpt("#line", optList))

	// App
	watch(() => props.status, (newV, oldV) => {
		if (chartIDList.indexOf(newV) == -1) {

			let opt = setOpt(oldV, optList)
			chart = ECharts.init(document.querySelector(".vc"))

			chart.setOption(opt, true)
			chart.resize()
		} else {
			let opt = setOpt(newV, optList)

			if (opt == false) {
				return 0
			}

			chart = ECharts.init(document.querySelector(".vc"))
			chart.setOption(opt, true)
			chart.resize()
		}
	})
})

</script>

