<!-- eslint-disable no-unused-vars -->
<template>
	<div class="viewModule">
		<div class="title">
			<h1>{{ msg }}</h1>
		</div>
		<ul>
			<li ref="line" id="line"></li>
			<li ref="bar" id="bar"></li>
			<li ref="columnBar" id="columnBar"></li>
			<li ref="bar_line" id="bar_line"></li>
			<li ref="pie" id="pie"></li>
			<li ref="pied" id="pied"></li>
			<li ref="scatters" id="scatters"></li>
			<li ref="scatter" id="scatter"></li>
		</ul>
	</div>
</template>

<script setup>
import { reactive, ref, onMounted, defineProps, shallowRef } from 'vue';
import * as EChartS from "@/assets/echarts.min.js"

import LineProcess from '@/js/LineProcess'
import OptionList from '@/js/OptionList'
import PieProcess from '@/js/PieProcess'
import BarProcess from '@/js/BarProcess'
import ColumnBarProcess from '@/js/ColumnBarProcess'
import BarLineProcess from '@/js/BarLineProcess'
import PiedProcess from '@/js/PiedProcess'
import ScattersProcess from '@/js/ScattersProcess'
import ScatterProcess from '@/js/ScatterProcess'

const props = defineProps(['msg'])

const activeData = reactive({
	chartList: Array()
})

const shallowData = shallowRef({
	chartList: Array()
})

// OptList
function processing(optList) {
	new LineProcess(optList).ready()
	new PieProcess(optList).ready()
	new BarProcess(optList).ready()
	new ColumnBarProcess(optList).ready()
	new BarLineProcess(optList).ready()
	new PiedProcess(optList).ready()
	new ScattersProcess(optList).ready()
	new ScatterProcess(optList).ready()
}

//  chartresize
window.addEventListener("resize", () => {
	shallowData.value.chartList.forEach(x => {
		x.resize()
	})
})

onMounted(() => {
	let chartIDList = Array('#line', '#bar', '#columnBar', '#bar_line', '#pie', '#pied', '#scatters', '#scatter')

	//  
	let optList = new OptionList()
	// option
	processing(optList)

	// 
	chartIDList.forEach(x => {
		shallowData.value.chartList.push(EChartS.init(document.querySelector(x)))
	})

	for (let i = 0; i < shallowData.value.chartList.length; i++) {
		if (optList.retOpt(chartIDList[i]) == false) {
			continue
		}
		shallowData.value.chartList[i].setOption(optList.retOpt(chartIDList[i]))
	}
})


</script>

