import ProcessData from "./ProcessData";

/*
title: {} > text: ""
tooltip: {} > trigger(): "axis"  
legend: {} > data: [] 
xAxis: {} > type 
*/

class LineProcess extends ProcessData {
	constructor(list, data=null) {
		super(list, data)
		console.log("Line" ,data)
		console.log("Line" ,list)
	}

	processFunction() {
		
	}

	optionFunction() {
		super.data = {
			title: {
				"text": "HEI"
			},
			legend: {
				data: ['A', 'B']
			},
			tooltip: {
				trigger: 'axis'
			},
			xAxis: {
				// type: 'category',
				data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
			},
			yAxis: {
				type: 'value'
			},
			series: [
				{
					name: "A",
					data: [150, 230, 224, 218, 135, 147, 260],
					type: 'line'
				},
				{
					name: "B",
					data: [200, 130, 524, 118, 135, 177, 460],
					type: 'line'
				}
			]
		}
		
		super.insertOpt("#line")
	}

	ready() {
		this.processFunction()
		this.optionFunction()
	}
}

export default LineProcess