export class BaseObj {
	id = this.id
	data = {}

	constructor(in_id, in_data) {
		this.id = in_id
		this.data = in_data
	}
}

class OptionList {
	optList = null
	constructor() {
		this.optList = []
	}
	
	generateBaseObj(id, data) {
		return new BaseObj(id, data)
	}

	addOpt(id, data) {
		this.optList.push(this.generateBaseObj(id, data))
	}

	resetOpt(indexID, data) {
		this.optList.forEach(element => {
			if (element.id == indexID) {
				element.data = data
			}
		});
	}

	retOptList() {
		return this.generateBaseObj()
	}

	retOpt(indexID) {
		let indexValue = null
		this.optList.forEach(element => {
			if (element.id == indexID) {
				// console.log("RETOPT", element.data)
				indexValue = element.data
			}
		})

		if (indexValue == null) {
			return false
		}
		return indexValue
	}
}

export default OptionList